	module.exports = {"data":{"challengeNode":{"title":"Create a Block Element Bootstrap Button","description":["一般情况下，具有 <code>btn</code> 和 <code>btn-default</code> 两个 class 的 <code>button</code> 元素宽度与它包含的文本相同，举个例子：","<code>&lt;button class=\"btn btn-default\"&gt;Submit&lt;/button&gt;</code>","这个按钮的宽度应该和文本 \"Submit\" 相同","<button class='btn btn-default'>Submit</button>","通过为按钮添加 class 属性 <code>btn-block</code> 使其成为块级元素，按钮会伸展并填满页面整个水平空间，后续的元素会流到这个块级元素的下方，即 \"新开一行\"。","<code>&lt;button class=\"btn btn-default btn-block\"&gt;Submit&lt;/button&gt;</code>","这个按钮会 100% 占满所有的可用宽度。","<button class='btn btn-default btn-block'>Submit</button>","记住这些按钮仍然需要 <code>btn</code> 这个 class。","添加 Bootstrap 的 <code>btn-block</code> class 到你 Bootstrap 按钮上吧。"],"challengeType":0,"fields":{"slug":"/front-end-libraries/bootstrap/create-a-block-element-bootstrap-button","blockName":"Bootstrap","tests":[{"text":"按钮的 class 属性应该仍然具有 <code>btn</code> 和 <code>btn-default</code>。","testString":"assert($(\"button\").hasClass(\"btn\") && $(\"button\").hasClass(\"btn-default\"), '按钮的 class 属性应该仍然具有 <code>btn</code> 和 <code>btn-default</code>。');"},{"text":"按钮的 class 属性应该包含 <code>btn-block</code>。","testString":"assert($(\"button\").hasClass(\"btn-block\"), '按钮的 class 属性应该包含 <code>btn-block</code>。');"},{"text":"确保所有的 <code>button</code> 元素都有一个闭合标签。","testString":"assert(code.match(/<\\/button>/g) && code.match(/<button/g) && code.match(/<\\/button>/g).length === code.match(/<button/g).length, '确保所有的 <code>button</code> 元素都有一个闭合标签。');"}]},"required":[{"link":"https://cdnjs.cloudflare.com/ajax/libs/twitter-bootstrap/3.3.7/css/bootstrap.css","raw":null,"src":null}],"files":{"indexhtml":{"key":"indexhtml","ext":"html","name":"index","contents":"<link href=\"https://fonts.googleapis.com/css?family=Lobster\" rel=\"stylesheet\" type=\"text/css\">\n<style>\n  .red-text {\n    color: red;\n  }\n\n  h2 {\n    font-family: Lobster, Monospace;\n  }\n\n  p {\n    font-size: 16px;\n    font-family: Monospace;\n  }\n\n  .thick-green-border {\n    border-color: green;\n    border-width: 10px;\n    border-style: solid;\n    border-radius: 50%;\n  }\n\n  .smaller-image {\n    width: 100px;\n  }\n</style>\n\n<div class=\"container-fluid\">\n  <h2 class=\"red-text text-center\">CatPhotoApp</h2>\n\n  <p>Click here for <a href=\"#\">cat photos</a>.</p>\n\n  <a href=\"#\"><img class=\"smaller-image thick-green-border\" src=\"https://bit.ly/fcc-relaxing-cat\" alt=\"A cute orange cat lying on its back.\"></a>\n\n  <img src=\"https://bit.ly/fcc-running-cats\" class=\"img-responsive\" alt=\"Three kittens running towards the camera.\">\n  <button class=\"btn btn-default\">Like</button>\n  <p>Things cats love:</p>\n  <ul>\n    <li>cat nip</li>\n    <li>laser pointers</li>\n    <li>lasagna</li>\n  </ul>\n  <p>Top 3 things cats hate:</p>\n  <ol>\n    <li>flea treatment</li>\n    <li>thunder</li>\n    <li>other cats</li>\n  </ol>\n  <form action=\"/submit-cat-photo\">\n    <label><input type=\"radio\" name=\"indoor-outdoor\"> Indoor</label>\n    <label><input type=\"radio\" name=\"indoor-outdoor\"> Outdoor</label>\n    <label><input type=\"checkbox\" name=\"personality\"> Loving</label>\n    <label><input type=\"checkbox\" name=\"personality\"> Lazy</label>\n    <label><input type=\"checkbox\" name=\"personality\"> Crazy</label>\n    <input type=\"text\" placeholder=\"cat photo URL\" required>\n    <button type=\"submit\">Submit</button>\n  </form>\n</div>","head":"","tail":""},"indexjs":null,"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[{"link":"https://cdnjs.cloudflare.com/ajax/libs/twitter-bootstrap/3.3.7/css/bootstrap.css","raw":null,"src":null}],"nextChallengePath":"/front-end-libraries/bootstrap/taste-the-bootstrap-button-color-rainbow","id":"bad87fee1348cd8acef08812"},"slug":"/front-end-libraries/bootstrap/create-a-block-element-bootstrap-button"}}

